import React from 'react';
import Paper from '@mui/material/Paper';
import Grid from '@mui/material/Grid';
import { styled } from '@mui/material/styles';

import CompaniesListing from './Companies';
import CustomSeachBlock from './SearchBlock';
import CompanyInformation from './CompanyInformation';

const Item = styled(Paper)(({ theme }) => ({
    backgroundColor: theme.palette.mode === 'dark' ? '#1A2027' : '#fff',
    ...theme.typography.body2,
    padding: theme.spacing(1),
    textAlign: 'center',
    color: theme.palette.text.secondary,
}));

const convertToCompanyList = (apiResponse) => {
    return apiResponse.map(data => {
        return {
            symbol: data["1. symbol"],
            name: data["2. name"],
            type: data["3. type"],
            region: data["4. region"]
        };
    });
};

export default function ListedCompaniesBlock() {

    const [companySearchTerm, setCompanySearchTerm] = React.useState('GOOG');
    const [companiesListData, setCompaniesListData] = React.useState({isLoading: true, error: null, companies: []});
    const [selectedCompanySymbol, setSelectedCompanySymbol] = React.useState('GOOG');

    const onSubmitCompanyTerm = (term) => {
        setCompanySearchTerm(term);
        setCompaniesListData({
            ...companiesListData,
            isLoading: true
        });
    };

    const onClickCompany = (companySymbol) => {
        console.log("Company Symbol Clicked: " + companySymbol);
        setSelectedCompanySymbol(companySymbol);
    };

    React.useEffect(() => {
        if(!companySearchTerm) {
            return;
        }
        const API_KEY = 'JKCWI02F50XDCM1A';
        const LISTING_URL = `https://www.alphavantage.co/query?function=SYMBOL_SEARCH&keywords=${companySearchTerm}&apikey=${API_KEY}`;
        fetch(LISTING_URL).then(result => result.json()).then(data => {
            const processedData = convertToCompanyList(data['bestMatches']);
            setCompaniesListData({
                ...companiesListData,
                companies: processedData,
                isLoading: false
            });
        }).catch(err => {
            setCompaniesListData({
                ...companiesListData,
                error: err
            })
        });
    }, [companySearchTerm]);

    return (
        <>
            <Grid item xs={6}>
                <Item>
                    <CustomSeachBlock value={companySearchTerm} onSubmitValue={onSubmitCompanyTerm} />
                    <CompaniesListing companiesData={companiesListData} onSelectCompany={onClickCompany} />
                </Item>
            </Grid>
            <Grid item xs={6}>
                <Item>
                    <CompanyInformation companySymbol={selectedCompanySymbol} />
                </Item>
            </Grid>
        </>
    );
}