import React from 'react';

import { styled } from '@mui/material/styles';
import Card from '@mui/material/Card';
import CardHeader from '@mui/material/CardHeader';
import CardContent from '@mui/material/CardContent';
import CardActions from '@mui/material/CardActions';
import Collapse from '@mui/material/Collapse';
import Avatar from '@mui/material/Avatar';
import IconButton from '@mui/material/IconButton';
import Typography from '@mui/material/Typography';
import { red } from '@mui/material/colors';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import { Chip } from '@mui/material';
import CompanyQuotes from './CompanyQuotes';

const convertToCompanyInformation = (apiResponse) => {
    return {
        symbol: apiResponse["Symbol"],
        sector: apiResponse["Sector"],
        name: apiResponse["Name"],
        description: apiResponse["Description"],
        fiftyDayMovingAverage: apiResponse["50DayMovingAverage"],
        fiftyTwoWeekHigh: apiResponse["52WeekHigh"],
        fiftyTwoWeekLow: apiResponse["52WeekLow"]
    };
}

const convertToCompanyQuote = (apiResponse) => {
    return {
        symbol: apiResponse["01. symbol"],
        openingPrice: apiResponse["02. open"],
        dayHigh: apiResponse["03. high"],
        dayLow: apiResponse["04. low"],
        closingPrice: apiResponse["05. price"],
        volume: apiResponse["06. volume"],
        previousDayClosingPrice: apiResponse["08. previous close"],
        change: apiResponse["09. change"],
        changePercent: apiResponse["10. change percent"]
    };
};

const ExpandMore = styled((props) => {
    const { expand, ...other } = props;
    return <IconButton {...other} />;
  })(({ theme, expand }) => ({
    transform: !expand ? 'rotate(0deg)' : 'rotate(180deg)',
    marginLeft: 'auto',
    transition: theme.transitions.create('transform', {
      duration: theme.transitions.duration.shortest,
    }),
  }));  


export default function CompanyInformation({companySymbol = ''}) {
    const [companyInformation, setCompanyInformation] = React.useState({});
    const [isLoading, setIsLoading] = React.useState(true);
    const [error, setError] = React.useState({});

    const [expanded, setExpanded] = React.useState(false);
    const handleExpandClick = () => {
        if(expanded === true) {
            setExpanded(!expanded);
        }
        else {
            const API_KEY = 'JKCWI02F50XDCM1A';
            const COMPANY_QUOTE_ENDPOINT = `https://www.alphavantage.co/query?function=GLOBAL_QUOTE&symbol=${companySymbol}&apikey=${API_KEY}`;
            fetch(COMPANY_QUOTE_ENDPOINT)
                .then(result => result.json())
                .then(result => {
                    companyInformation["quotes"] = convertToCompanyQuote(result["Global Quote"]);
                    setExpanded(!expanded);
                })
        }
    };

    const handleOverviewAPIResponse = (response) => {
        if('Note' in response || Object.keys(response).length === 0 ) {
            setError("You have reached API limit. Please try after some time.");
            return;
        }
        setCompanyInformation(convertToCompanyInformation(response));
        setIsLoading(false);
        setError({});
    }

    React.useEffect(() => {
        if(!companySymbol) {
            return;
        }
        setIsLoading(true);
        setCompanyInformation({});
        const API_KEY = 'JKCWI02F50XDCM1A';
        const COMPANY_OVERVIEW_ENDPOINT = `https://www.alphavantage.co/query?function=OVERVIEW&symbol=${companySymbol}&apikey=${API_KEY}`;
        fetch(COMPANY_OVERVIEW_ENDPOINT)
            .then(result => result.json())
            .then(result => handleOverviewAPIResponse(result))
            .catch(setError);
    }, [companySymbol]);

    if(Object.keys(error).length !== 0) {
        return <pre>{JSON.stringify(error, null, 2)}</pre>;
    }

    if(isLoading || Object.keys(companyInformation).length === 0) {
        return (<p>Please wait. We are loading company information...</p>);
    }

    return (
        <Card>
            <CardHeader
                avatar={
                <Avatar sx={{ bgcolor: red[500] }} aria-label="recipe">
                    {companyInformation.symbol[0]}
                </Avatar>
                }
                title={companyInformation.name}
                subheader={companyInformation.sector}
            />
            <CardContent>
                <Typography variant="body2" color="text.secondary">
                    {companyInformation.description}
                </Typography>
            </CardContent>
            <CardActions disableSpacing>
                <Chip label={`50 Day Average: $${companyInformation.fiftyDayMovingAverage} `} variant="outlined" />
                <Chip label={`52 Weeks High: $${companyInformation.fiftyTwoWeekHigh}`} variant="outlined" />
                <Chip label={`52 Weeks Low: $${companyInformation.fiftyTwoWeekLow}`} variant="outlined" /> 
                <ExpandMore
                expand={expanded}
                onClick={handleExpandClick}
                aria-expanded={expanded}
                aria-label="show more"
                >
                <ExpandMoreIcon />
                </ExpandMore>
            </CardActions>
            <Collapse in={expanded} timeout="auto" unmountOnExit>
                <CardContent>
                    <CompanyQuotes companyInfo={companyInformation} />
                </CardContent>
            </Collapse>
        </Card>
    );
}


