import React from 'react';
import { DataGrid } from '@mui/x-data-grid';

function CompaniesListing({companiesData, onSelectCompany= f => f}) {

    const columns = [
        { field: 'symbol', headerName: 'Symbol' },
        { field: 'name', headerName: 'Company Name', width: 250 },
        { field: 'region', headerName: 'Region', width: 250 },
        { field: 'type', headerName: 'Asset Type' }
      ];

    if(companiesData.error) {
        return <pre>{JSON.stringify(companiesData.error, null, 2)}</pre>;
    }
    if(companiesData.isLoading) {
        return (<p>Loading Companies listing...</p>);
    }
    else {
        return (<div>
            <h1>Search Results</h1>
            <div style={{ height: 650, width: '100%' }}>
                <DataGrid
                    rows={companiesData.companies}
                    columns={columns}
                    pageSize={10}
                    rowsPerPageOptions={[10]}
                    getRowId={(row) => row.symbol}
                    onCellClick={ ({id}) => onSelectCompany(id)}
                />
            </div>
        </div>);
    }
}

export default CompaniesListing;